div .inside {
  position: absolute;
  left: 50%;
  margin-top: 20px;
  transform: translate(-50%);
  width: 500px;
  height: 500px;

  border-radius: 20px;
  background-color: #e0e0e0;
  box-shadow: 0px 0px 11px 0px #000000;
}
.app {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}
