p {
  margin-top: 5px;
}
button {
  height: 2em;
  background-color: #808080;
  border: 1px solid black;
  margin-left: 5px;
  border-radius: 3px;
}

select {
  height: 2em;
  border: 1px solid black;
  margin-left: 5px;
  border-radius: 3px;
}

button:hover {
  background-color: #dcdcdc;
}
option {
  background-color: #f5f5f5;
}

option:checked {
  color: gray;
  font-weight: 700;
}

option:hover {
  box-shadow: 0 0 1px 3px rgba(222, 223, 222, 0.7);
}
