@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

div .inside {
  position: absolute;
  left: 50%;
  margin-top: 20px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 500px;
  height: 500px;

  border-radius: 20px;
  background-color: #e0e0e0;
  box-shadow: 0px 0px 11px 0px #000000;
}
.app {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

p {
  margin-top: 5px;
}
button {
  height: 2em;
  background-color: #808080;
  border: 1px solid black;
  margin-left: 5px;
  border-radius: 3px;
}

select {
  height: 2em;
  border: 1px solid black;
  margin-left: 5px;
  border-radius: 3px;
}

button:hover {
  background-color: #dcdcdc;
}
option {
  background-color: #f5f5f5;
}

option:checked {
  color: gray;
  font-weight: 700;
}

option:hover {
  box-shadow: 0 0 1px 3px rgba(222, 223, 222, 0.7);
}

i {
  font-size: 1em;
  font-weight: 700;
  margin-bottom: 1em;
  font-style: normal;
}

h2 {
  margin-top: 1em;
  font-size: 2em;
  text-align: center;
}

img {
  position: relative;

  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
}

li {
  list-style-type: none;
}
ul {
  width: 400px;
  height: 300px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.info {
  align-items: flex-start;
}
.img {
  margin-top: 1em;
  border: 1px;
  width: 110px;
  height: 110px;
  background-color: gray;

  border-radius: 5px;
  box-shadow: 0px 0px 4px 0px #000000;
}
.descr {
  margin-top: 1em;
  width: 210px;
  height: 40px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

