i {
  font-size: 1em;
  font-weight: 700;
  margin-bottom: 1em;
  font-style: normal;
}

h2 {
  margin-top: 1em;
  font-size: 2em;
  text-align: center;
}

img {
  position: relative;

  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
}

li {
  list-style-type: none;
}
ul {
  width: 400px;
  height: 300px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.info {
  align-items: flex-start;
}
.img {
  margin-top: 1em;
  border: 1px;
  width: 110px;
  height: 110px;
  background-color: gray;

  border-radius: 5px;
  box-shadow: 0px 0px 4px 0px #000000;
}
.descr {
  margin-top: 1em;
  width: 210px;
  height: 40px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
